/********** OUR PRODUCT **********/

#our_product {
  // height: 1080px;
  height: 900px;
  padding: 85px 0px 20px 0px;
  padding-bottom: 4px;
  background-color: rgb(236, 236, 236);
}
#our_product {
  .title {
    animation: moveToTop 2s running;
  }
}

#our_product {
  .gallery {
    display: grid;
    justify-content: center;
    // grid-template: repeat(2, 1fr) / repeat(4, 1fr);
    grid-template: repeat(2, 300px) / repeat(4, 380px);
    gap: 20px;

    .gridItem {
      // height: 50%;
      height: 300px;
      // margin: 153px;
      animation: moveToTop 2s running;
      &:nth-child(1) {
        grid-column: 1/3;
        grid-row: 1/3;
        height: 620px;
      }
    }
  }
}
#our_product {
  .gridWrap {
    .grid_img1 {
      // background: url(./images/m-op01.jpg) no-repeat 50% / cover;
      grid-row: 1 / 3;
      grid-column: 1 / 3;
    }
  }
}

