#our_product {
  .gridWrap {
    .bg {
      color: #fff;
      text-align: center;
      width: 100%;
      position: relative;
    }
  }
}

#our_product {
  .gridWrap {
    a {
      h4 {
        color: #fff;
        font-size: 16px;
        transform: translateY(85px);
        animation: move_h4 1s;
        display: none;
      }
    }
  }
}

#our_product {
  .gridWrap {
    a:first-child {
      h4 {
        color: #fff;
        font-size: 20px;
        transform: translateY(180px);
        animation: move_h4_2 1s;
        display: none;
      }
    }
  }
}

#our_product {
  .gridWrap {
    a {
      p {
        font-size: 9px;
        border: 1.5px solid #fff;
        padding: 3px 15px 4px;
        width: 60px;
        margin: 0 auto;
        transform: translateY(95px);
        animation: move_p 1s;
        display: none;
      }
    }
  }
}

#our_product {
  .gridWrap {
    a:first-child {
      p {
        transform: translateY(190px);
        animation: move_p_2 1s;
        display: none;
      }
    }
  }
}

#our_product {
  .gridWrap {
    .bg:hover::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      backdrop-filter: brightness(50%);
      z-index: 99;
    }
  }
}

#our_product {
  .gridWrap {
    .bg:hover {
      h4 {
        display: block;
        position: relative;
        z-index: 999;
      }
    }
  }
}

#our_product {
  .gridWrap {
    .bg:hover {
      p {
        display: block;
        position: relative;
        z-index: 999;
      }
    }
  }
}

